import React from 'react';
import { InputGroup, InputGroupText, InputGroupAddon, Input } from 'reactstrap';

const Example = (props) => {
  return (
    <div>
      <InputGroup>
        <Input />
        <InputGroupAddon addonType="append" placeholder="클럽명 검색">
          <InputGroupText>검색</InputGroupText>
        </InputGroupAddon>
      </InputGroup>     
    </div>
  );
};

export default Example;


